@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.h1,
.h2,
.h3,
.h4 {
  margin: 10px;
}
.h1 {
  font-size: 3rem;
}
.h2 {
  font-size: 2rem;
}

.h3 {
  font-size: 1.7rem;
}
.h4 {
  font-size: 1.3rem;
}
.base {
  font-size: 1rem;
}




.container {
  display: flex;
  overflow: hidden;
  transition: var(--transition-speed);
}
.container[expanded='false'] {
  height: 0px !important;
  padding: 0 !important;
}

button.button {
  padding: 5px;
  border-radius: var(--border-radius);
  border: none;
}
button.button {
  color: var(--text-white);
  background: var(--text-primary);
  padding: 10px 20px;
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 1.3rem;
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}
button:hover {
  -webkit-filter: grayscale(0.5);
          filter: grayscale(0.5);
}
button:active {
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}
button p.textography {
  display: block;
  margin: 0px !important;
}
button.small {
  font-size: 1rem;
  padding: 4px;
  margin: 2px;
}

.card {
  background-color: var(--bg-secondary-dark);
  position: relative;
  overflow: hidden;
  border-radius: var(--border-radius);
  padding: 0px;
  z-index: 0;
  min-width: 30vw;
}
.card-title {
  background-color: var(--bg-primary-light);
  display: block;
  margin: 0px;
  padding: 2px;
  width: 100%;
}
.card-title p {
  display: block;
  padding: 5px;
  min-width: 150px;
  margin: 0px;
}
.card > p {
  padding: 10px;
}
.card .main-content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 10px;
  justify-content: center;
}
.card-footer {
  background-color: var(--bg-secondary-light);
  display: flex;
  box-sizing: border-box;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  margin: 0px 0px 0px 0px;
  padding: 0px 10px;
  top: 0px;
  width: 100%;
}
.card-footer p {
  text-align: right;
  font-size: 0.8rem;
  display: block;
  margin-right: 10px;
}
.card .card-footer svg {
  transition: var(--transition-speed);
}
.card[expanded='true'] svg {
  transform: rotate(0deg);
}
.card[expanded='false'] svg {
  transform: rotate(180deg);
}

input.input-field {
  display: flex;
  color: var(--text-primary);
  justify-content: center;
  padding: 5px;
  font-size: 1.3rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: var(--border-radius);
  width: 90%;
  background-color: var(--bg-secondary-light) !important;
}

.input-wrapper:first-child {
  border-top-left-radius: calc(var(--border-radius) * 2);
  border-top-right-radius: calc(var(--border-radius) * 2);
}
.input-wrapper:last-child {
  border-bottom-left-radius: calc(var(--border-radius) * 2);
  border-bottom-right-radius: calc(
    var(--border-radius) * 2
  );
}
.input-wrapper {
  width: 100%;

  padding: 0 15px 15px 0;
  margin: 0 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.input-label {
  width: 100%;
  display: block;
  text-align: left;
}

.card {
  width: 100%;
}
.content-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  grid-gap: 20px;
  gap: 20px;
}
.signup-form-card {
  border-radius: var(--border-radius);
}
.signup-form-card .main-content .textography {
  width: 100%;
  margin-left: 2.5vw;
}
.signup-form-card img.card-title-image {
  -webkit-filter: grayscale(0.8) brightness(0.8);
          filter: grayscale(0.8) brightness(0.8);
  position: absolute;
  top: 50%;
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: 0;
  margin-top: calc(25px - 50%);
  left: 0;
  margin-left: auto;
  z-index: 1;
  transform: scale(1.1);
  transition: all 0.6s ease;

  transform: rotate(0deg);

  animation-name: rotate;
  animation-duration: 30s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(10deg) scale(1.1);
  }
  100% {
    transform: rotate(0deg) scale(1);
  }
}
.signup-form-card .card-title p {
  display: inline-block;
  padding: 0;
  margin: 0px;
  padding: 5px;
  z-index: 2;
  position: relative;
  background: rgba(0, 0, 0, 0.6);
  width: 30%;
}
.signup-form-card .card-title {
  background-color: var(--bg-primary-light);
  display: block;
  margin: 0px;
  width: 100%;
  overflow: hidden;
  padding: 0px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
@media only screen and (max-width: 600px) {
  .signup-form-card {
    width: 95vw;
    border-radius: var(--border-radius);
  }
}

@media only screen and (min-width: 600px) {
  .signup-form-card {
    width: 50vw;
    max-width: 600px;
  }
}

