.raffle-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.raffle-info p.textography.h2 {
  width: 100%;
  display: block;
}
.button-section {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}
p.ticket-number.textography.base {
  background: var(--bg-secondary-light);
  padding: 5px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}
.rigger .ticket-not-confirmed {
  background: #aa2222;
}
.rigger .ticket-confirmed {
  background: green;
  color: white;
}
.rigger > .h4 {
  display: block;
  width: 270px;
  text-align: center;
  padding: 10px;
  border-radius: 4px;
}

nav.navbar {
  position: fixed;
  background: var(--bg-secondary-main);
  transition: width 200ms ease;
  overflow: hidden;
  white-space: nowrap;
}

nav.navbar .nav-item:hover {
  background: var(--bg-secondary-light);
  transition: var(--transition-speed);
}
nav.navbar .nav-link:hover svg,
nav.navbar .nav-link:hover .link-text,
#active-nav-item svg,
#active-nav-item .link-text {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
}
.nav-ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
}
.nav-item {
  display: flex;
  width: 100%;
}

#active-nav-item {
  background: var(--bg-primary-light);
}

.nav-item:last-child {
  margin-top: auto;
}

.nav-link {
  width: 100%;
  max-height: 5em;
  display: flex;
  color: var(--text-primary);
  text-decoration: none;
  align-items: center;
  height: 15vh;
}
.nav-link .link-icon svg {
  min-width: 2.3rem;
  font-size: 1.6rem;
  padding: 0;
  margin: 0 1.5rem;
}
.link-text {
  margin-left: 1rem;
  display: none;
}
.nav-link .link-icon svg,
.nav-link .link-text {
  color: var(--text-primary);
  transition: var(--transition-speed);
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
}
.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  letter-spacing: 0.3ch;
  width: 100%;
  background: var(--bg-primary-light);
}

.logo a.nav-link > span svg,
.logo a.nav-link > span {
  color: var(--text-primary);
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
}
.logo svg {
  transform: rotate(0deg);
  transition: transform var(--transition-speed);
}
.navbar:hover .logo svg {
  transform: rotate(360deg);
}
@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5m;
  }
  .logo {
    display: none;
  }
  .nav-ul {
    flex-direction: row;
  }
  .nav-link {
    flex-direction: row;
    justify-content: center;
  }
}

@media only screen and (min-width: 600px) {
  nav.navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }
  nav.navbar:hover {
    width: 16rem;
  }
  nav.navbar:hover .link-text {
    display: block;
  }
}

html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
}
ul,
li {
  margin: 0;
  padding: 0;
}
#root {
  padding-bottom: 5rem;
}
main {
  margin-left: 5rem;
  padding: 1rem;
  z-index: 0;
}
nav.navbar {
  z-index: 2;
}
.elevated {
  position: relative;
  box-shadow: 0px calc(var(--elev) * 0.5px)
    calc(var(--elev) * 0.5px) calc(var(--elev) * 0.25px)
    rgba(0, 0, 0, calc(var(--elev) * 0.1));
  transition: var(--transition-speed);
}
.elevated > * {
  position: relative;
}
.elevated::before {
  z-index: 0;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(
    255,
    255,
    255,
    calc(var(--elev) * 0.01)
  );
  transition: var(--transition-speed);
}
* {
  outline: none;
}
/* Change the white to any color ;) */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  border-radius: 0;
  -webkit-box-shadow: 0 0 0 30px var(--bg-secondary-light)
    inset !important;

  padding: 5px;
  border: none;
}

.warning {
  color: var(--warning);
}
.error {
  color: var(--error);
}
.critical {
  color: var(--critical);
}

/*Change text in autofill textbox*/
input:-webkit-autofill {
  -webkit-text-fill-color: var(--text-secondary) !important;
}

@media only screen and (max-width: 600px) {
  main {
    margin-left: 0rem;
  }
}

@media only screen and (min-width: 600px) {
  main {
    margin-left: 5rem;
  }
}

/** Custom Dropdown Style **/
select {
  padding: 10px;
  background: var(--bg-primary-light);
  color: var(--text-primary);
  font-size: 1rem;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

:root {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  --text-white: #ebeceb;
  --text-primary: #3ee45c;
  --text-secondary: #fb98fb;
  --bg-primary-light: #414142;
  --bg-primary-main: #2d2d2e;
  --bg-primary-dark: #121216;
  --bg-secondary-light: #28282c;
  --bg-secondary-main: #141418;
  --bg-secondary-dark: #121216;
  --border-primary-dark: #5235a4;
  --border-primary-main: #6649b8;
  --border-primary-light: #7a5dcc;
  --warning:#ff8b00;
  --error:#ff0033;
  --critical:#e46a43;
  --border-radius: 5px;
  --transition-speed: 600ms ease-out;
}
body {
  background: var(--bg-primary-main);
  color: var(--text-primary);
}
body::-webkit-scrollbar {
  width: 0.25rem;
}
body::-webkit-scrollbar-track {
  background: var(--bg-secondary-main);
}
body::-webkit-scrollbar-thumb {
  background: var(--border-primary-main);
}

